import React from "react"
import Navbar from "../components/navbar"
import Card from "react-bootstrap/Card"
import { Container, Row, Col, Image, OverlayTrigger, Tooltip} from "react-bootstrap"
import RealNavbar from "../components/real_navbar"

export default function Projects(){
    return (
        <div>
            <link
            rel="stylesheet"
            href="https://maxcdn.bootstrapcdn.com/bootstrap/4.5.0/css/bootstrap.min.css"
            integrity="sha384-9aIt2nRpC12Uk9gS9baDl411NQApFmC26EwAOH8WgZl5MYYxFfc+NcPb1dKGj7Sk"
            crossorigin="anonymous"
            />

        <RealNavbar/>
        
        <br/>
        <Container fluid>
                <Col>
                <h2> FIEA Projects</h2>
                <br/>
                    <Card>
                    <Card.Header as="h5">K3LVN</Card.Header>

                    <Card.Body>
                        <Card.Text>
							<br/>
							Over the course of 8 months I acted as Lead Programmer on my graduate capstone project, K3LVN, a 3D action
                            platformer built in Unreal Engine 4.
							<br/>
                        </Card.Text>
						<Card.Link href="">Steam</Card.Link>
                    </Card.Body>
                    </Card>

					<br/>

                    <br/>

                    <br/>
                </Col>

        </Container>


            
        </div>
    )
}